export default {
	props: {
		list: {
			type: Array,
			default: []
		}
		
	},
	data() {
		return {
			selIndex: 0
		}
	},
	onLoad() {
	},
	methods: {
		radioChange(val, index) {
			this.selIndex = index;
			this.$emit('radioChange', val);
		},
		okRadioValue () {
			this.$emit('okRadioValue');	
		}
	}
	
}
